@use "../../styles/abstracts" as *;

.wrapper {
  display: grid;
  grid-template-columns: 1fr 20px 1fr;
  width: 100%;
  gap: 2rem;
}

.services_list_wrapper {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
}

.services_list_container {
  display: grid;
  grid-template-columns: 1fr 20px 1fr;
  gap: 2rem;
}