@use "../../styles/abstracts" as *;

.employee_container {
  position: relative;
  cursor: pointer;
}

.mask {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.47);
  z-index: 99;
  transition: .5s;
}

.mask:hover {
  background: none;
}



.rating {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 1rem;
}

.employee_checked {
  position: absolute;
  color: $cl-green;
  right: 0;
  top: 0;
  z-index: 99;
}


.employee_checked_delete {
  display: none;
  position: absolute;
  color: $cl-primary;
  right: 0;
  top: 0;
  z-index: 99;
}

.employee_container:hover .employee_checked {
  display: none;
}

.employee_container:hover .employee_checked_delete {
  display: block;
}