@use "../../styles/abstracts" as *;

.orders_container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.orders_home_container {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
  padding: 4rem 0;
}

.orders_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.orders_content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  gap: 1rem;
}